:root {
  --blue-color: #00acdc;
}

* {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 72px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo-link {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

.logo-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.nav-list {
  margin-right: 50px;
}

.nav-list ul {
  text-transform: uppercase;
  list-style: none;
  display: flex;
}

.nav-list li {
  margin-left: 20px;
}

.nav-list li:first-child {
  margin-left: 0;
}

.nav-list li:last-child {
  margin-right: 0;
}

.menu-link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-decoration: none;
  display: flex;
  position: relative;
}

.menu-link:hover, .menu-link.is-active {
  color: var(--blue-color);
}

.menu-link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
